import React, { useState, useEffect } from 'react';
import axios from 'axios';

const UserUpdater = () => {
  const [userId, setUserId] = useState('');
  const [tableName, setTableName] = useState('');
  const [userData, setUserData] = useState({});

  // Fetch user data when userId or tableName changes
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await axios.get(`http://13.126.253.132:5000/api/${tableName}/usersupdate1/${userId}`);
        setUserData(response.data);
      } catch (error) {
        console.error(error);
      }
    };

    if (userId && tableName) {
      fetchUserData();
    }
  }, [userId, tableName]);

  const handleChange = event => {
    setUserData({
      ...userData,
      [event.target.name]: event.target.value
    });
  };

  const handleSubmit = async event => {
    event.preventDefault();
    try {
      const response = await axios.post(`http://13.126.253.132:5000/api/save-changes-user/${tableName}`, { userId, tableData: [userData] });
      console.log(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div>
      <h1>Update User</h1>
      <form onSubmit={handleSubmit}>
        <label htmlFor="tableName">Table Name:</label>
        <input type="text" id="tableName" onChange={e => setTableName(e.target.value)} value={tableName} />

        <label htmlFor="userId">User ID:</label>
        <input type="text" id="userId" onChange={e => setUserId(e.target.value)} value={userId} />

        {/* Dynamically generate form fields based on userData */}
        {Object.entries(userData).map(([key, value]) => (
          <div key={key}>
            <label htmlFor={key}>{key}:</label>
            <input type="text" id={key} name={key} onChange={handleChange} value={value || ''} />
          </div>
        ))}

        <button type="submit">Save Changes</button>
      </form>
    </div>
  );
};

export default UserUpdater;