import React, { useEffect, useRef, useState } from "react";
import '../audio-module.css';
import visualGif from '../images/visual.gif';
import Cookies from 'js-cookie';
import AudioPlayerMain from './Audio'; // Adjust the path as needed
import logo from '../images/GCC-TBC.png';
import TrialAudioPlayer from "./Test";

const decodeBase64 = (encodedString) => {
    const decodedString = atob(encodedString);
    return decodedString;
  };

function AudioPlayer() {
    const audioRef = useRef(null);
    const [percentage, setPercentage] = useState(0);
    const [isPlaying, setIsPlaying] = useState(false);
    const [showMessageBox, setShowMessageBox] = useState(false);
    const [showAudioPlayerMain, setShowAudioPlayerMain] = useState(false);
    const [reload, setReload] = useState(false); // new state variable
    const [showInfoContainer, setShowInfoContainer] = useState(localStorage.getItem('showInfoContainer') === 'true');    

    const [audioSrc, setAudioSrc] = useState(''); // Initialize audioSrc state

    useEffect(() => {
        localStorage.setItem('showInfoContainer', showInfoContainer);
    }, [showInfoContainer]);

    useEffect(() => {
        setShowInfoContainer(!showAudioPlayerMain);
    }, [showAudioPlayerMain]);


// yes or noo say
    useEffect(() => {
      const UserId = Cookies.get('user_id'); // replace 'your_user_id' with the actual UserId
      fetch(`http://13.126.253.132:5000/api/testaudio1/${UserId}`)
        .then(response => response.json())
        .then(data => {
           // Update audioSrc state with the link from the response
  
          if (data === 'yes') {
            setShowAudioPlayerMain(true);
          }
        })
        .catch(error => console.error('Error:', error));
    }, []);

  
    useEffect(() => {
        const UserId = Cookies.get('user_id'); // replace 'your_user_id' with the actual UserId
        fetch(`http://13.126.253.132:5000/api/testaudioftech/${UserId}`)
          .then(response => response.json())
          .then(data => {
            const encodedLink = data.link;
            setAudioSrc(encodedLink); // Update audioSrc state with the link from the response
    
        
          })
          .catch(error => console.error('Error:', error));
      }, []);       
    

    useEffect(() => {
        const audio = audioRef.current;

        const updateProgressBar = () => {
            setPercentage(Math.floor((100 / audio.duration) * audio.currentTime));
        };

        const handleAudioEnd = () => {
            setIsPlaying(false);
            setShowMessageBox(true);
            setShowInfoContainer(false); // Add this line
        };

        audio.addEventListener('timeupdate', updateProgressBar);
        audio.addEventListener('ended', handleAudioEnd);

        return () => {
            audio.removeEventListener('timeupdate', updateProgressBar);
            audio.removeEventListener('ended', handleAudioEnd);
        };
    }, []);

    

    const startPlaying = () => {
        if (!audioRef.current.paused) {
            return;
        }
        audioRef.current.play();
        setIsPlaying(true);
        setShowMessageBox(false);
    };
    const handleMessageBoxResponse = async (response) => {
        console.log(`User responded: ${response}`);
    
        if (response === 'yes') {
            setShowMessageBox(false);
            setShowAudioPlayerMain(true);
            try {
                const UserId =  Cookies.get('user_id');
                const now = new Date();
                const dateTime = now.toLocaleString(); // Convert to local date/time string
                const logInfo = {
                  user_id: UserId,
                  information: ` ${dateTime}`
                };
                await fetch(`http://13.126.253.132:5000/api/testaudio`, {
                  method: 'POST',
                  headers: { 'Content-Type': 'application/json' },
                  body: JSON.stringify(logInfo)
                });
               
              } catch (error) {
                console.error('Error:', error);
              }
              
        } else if (response === 'no') {
            const audio = audioRef.current;
            if (audio){
                audio.currentTime = 0;
                audio.play();
                setPercentage(0);
                setIsPlaying(true)
                
               
                
            }
            
            setShowMessageBox(false); // Hide the message box while the audio is playing again
            // Reload the page
            window.location.reload();
    
        }
        // No need to setShowMessageBox(true) here
    };

    return (
        <>
            <div className="container">
                {showInfoContainer && (
                    <>
                        <div className="information">Instructions</div>
                        <div className="info-container">
                            <div className="info-left">
                                <p>This is test audio to check and adjust speaker sound with students instruction. You can play this audio again if required. <br />
                                ही टेस्ट ऑडियो स्पीकर साउंड चेक करण्यासाठी आहे. यामध्ये विद्यार्थ्यांना सूचना दिलेल्या आहेत. गरज पडल्यास हि ऑडियो पुन्हा ऐकता येईल.</p>
                            </div>
                        </div>
                    </>
                )}
                {showAudioPlayerMain? (
                    <TrialAudioPlayer />
                ) : (
                    showMessageBox ? (
                        <div className="message-box">
                        <p>Did you hear that audio?</p>
                        <p>To proceed to exam press Yes. To listen to test audio again press No. <br />
                        परीक्षेसाठी पुढे जाण्यासाठी "Yes" करावे. टेस्ट ऑडियो पुन्हा ऐकण्यासाठी "No" करावे.</p>
                        <div className="message-box-options">
                            <button onClick={() => handleMessageBoxResponse('yes')} className="yes-button">Yes</button>
                            <button onClick={() => handleMessageBoxResponse('no')} className="no-button">No</button>
                        </div>
                        </div>
                    ) : (
                        <div>
                            <div className="title-bar">
                                <div className="info-title">
                                <img src={logo} alt="GCC-TBC" />
                                </div>
                                <h1>
                                    MSCE Pune
                                    Online Exam
                                </h1>
                            </div>
                            <div className="title-bar">
                                <div className="info-title">
                                    <img src={logo} alt="GCC-TBC" />
                                </div>
                                <h1>
                                    Instructions Audio Session
                                </h1>
                            </div>
                            {/* ... (other components and JSX) */}
                            <div className="audio-player" id='audio-container'>
                                <audio id="audio" ref={audioRef} src={audioSrc}  preload="metadata"></audio>
                                <div className="audio-controls">
                                    <div id="play-pause" className="play" onClick={startPlaying}>
                                        {isPlaying ? '⏸️' : '▶️'}
                                    </div>
                                    <div className="audio-progress">
                                        <progress id="progress-bar" max="100" value={percentage}></progress>
                                        <div id="percentage">{percentage}%</div>
                                    </div>
                                </div>
                                {isPlaying && <img key={visualGif} id="visualizer" src={visualGif} alt="Audio visualizer" />}
                            </div>
                        </div>
                    )
                )}
            </div>
            <div className="copyright">&copy; 2023</div>
        </>

    );
}

export default AudioPlayer;
