import React, { useState, useEffect } from 'react';
import * as XLSX from 'xlsx';
import '../UserLogs.css';

function UserLogs() {
  const [logs, setLogs] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');

  useEffect(() => {
    const fetchLogs = () => {
      fetch('http://13.126.253.132:5000/api/userlogs')
        .then(response => response.json())
        .then(data => {
          const sortedData = data.sort((a, b) => a.user_id - b.user_id);
          setLogs(sortedData);
        })
        .catch(error => console.error('Error:', error));
    }

    fetchLogs(); // Fetch when component mounts
    const intervalId = setInterval(fetchLogs, 5000); // Fetch every 5 seconds

    // Clean up function
    return () => clearInterval(intervalId);
  }, []);
  if (logs.length === 0) {
    return null;
  }

  let columnNames = Object.keys(logs[0]);
  columnNames = columnNames.filter(name => name !== 'user_id');
  columnNames.unshift('user_id');

  const filteredLogs = logs.filter(log =>
    Object.values(log).some(value =>
      value !== null && value.toString().toLowerCase().includes(searchTerm.toLowerCase()))
  );

  const download = () => {
    const ws = XLSX.utils.json_to_sheet(filteredLogs);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Logs");
    XLSX.writeFile(wb, "logs.xlsx");
  }

  return (
    
    <div className="userlogs-container">
      <button onClick={download} className="download-button">Download XLSX</button>
      <input
        type="text"
        placeholder="Search..."
        value={searchTerm}
        onChange={event => setSearchTerm(event.target.value)}
        className="search-input"
      />
      <table className="logs-table">
        <thead>
          <tr>
            {columnNames.map((columnName, index) => (
              <th key={index}>{columnName}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {filteredLogs.map((log, rowIndex) => (
            <tr key={rowIndex}>
              {columnNames.map((columnName, columnIndex) => (
                <td key={columnIndex}>{log[columnName]}</td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default UserLogs;